A,4BAAA;AAEA;EACE,yBAAA;EACA,YAAA;AA8lzBF;;AA3lzBA;EACE,yBAAA;EACA,cAAA;AA8lzBF;;AA3lzBA;EACE,yBAAA;EACA,YAAA;AA8lzBF;;AA3lzBA;EACE,yBAAA;EACA,YAAA;AA8lzBF;;AA3lzBA,yCAAA;AACA;;EAEE,yBAAA;EACA,YAAA;AA8lzBF;;AA3lzBA,+BAAA;AACA;EACE,yBAAA;AA8lzBF;;AA3lzBA;;EAEE,eAAA;AA8lzBF;;AA3lzBA,2BAAA;AACA;EACE,cAAA;EACA,aAAA;AA8lzBF;;AA3lzBA;EACE,yBAAA;AA8lzBF;;AA3lzBA;EACE,gBAAA;EACA,mBAAA;AA8lzBF;;AA3lzBA,+BAAA;AACA;EACE,cAAA;AA8lzBF;;AA3lzBA;EACE,YAAA;AA8lzBF","sourcesContent":["\n// Override bootstrap related variables\n@import 'variables';\n\n@import '../../node_modules/bootstrap/scss/bootstrap';\n@import '../../node_modules/mdbootstrap/scss/mdb';\n@import '../../node_modules/@fortawesome/fontawesome-free/scss/fontawesome';\n@import '../../node_modules/@fortawesome/fontawesome-free/scss/brands';\n@import '../../node_modules/@fortawesome/fontawesome-free/scss/regular';\n@import '../../node_modules/@fortawesome/fontawesome-free/scss/solid';\n@import '../../node_modules/selectize-bootstrap4-theme/scss/selectize.bootstrap4';\n@import '../../node_modules/tempusdominus-bootstrap-4/src/sass/tempusdominus-bootstrap-4';\n@import '../../node_modules/tags-input/tags-input.css';\n@import '../../node_modules/bs-stepper/dist/css/bs-stepper.css';\n@import '../../node_modules/datatables.net-bs4/css/dataTables.bootstrap4.css';\n\n// Extend selectize bootstrap theme\n.selectize-control {\n  @extend .selectize-control;\n\n  .option {\n    .description {\n      color: $gray-600;\n      display: block;\n      font-size: $font-size-sm;\n    }\n\n    &:hover {\n      .description {\n        color: $selectize-dropdown-option-color-active;\n      }\n    }\n\n    &.active,\n    &:active {\n      .description {\n        color: $selectize-dropdown-option-color-active;\n      }\n    }\n  }\n}\n\n// Search input on DataTable\n.dataTables_filter input {\n  background-color: transparent;\n  border: 0;\n  border-bottom: 1px solid $input-border-color;\n  border-radius: 0;\n  box-shadow: none;\n  box-sizing: content-box;\n  color: $input-color;\n  font-family: inherit;\n  font-size: $input-label-font-size;\n  outline: none;\n\n  // Focused input style\n  &:focus:not([readonly]) {\n    border-bottom: 1px solid $input-md-focus-color;\n    box-shadow: 0 1px 0 0 $input-md-focus-color;\n    // Focused label style\n    + label {\n      color: $input-md-focus-color;\n    }\n  }\n}\n\n// Length select on DataTable\n.dataTables_length {\n  border: 0;\n  position: relative;\n\n  .custom-select {\n    // Remove arrow on Firefox\n    -moz-appearance: none;\n    // Remove arrow on Chrome\n    -webkit-appearance: none;\n    appearance: none;\n    background: transparent;\n    border: 0;\n    border-bottom: 1px solid $input-border-color;\n    color: $input-color;\n\n    // Remove arrow on IE10\n    &::-ms-expand {\n      display: none;\n    }\n\n    &:focus {\n      outline: none;\n    }\n  }\n}\n\n.md-select {\n  // Remove arrow on Firefox\n  -moz-appearance: none;\n  // Remove arrow on Chrome\n  -webkit-appearance: none;\n  background-color: transparent;\n  border: 0;\n  border-bottom: 1px solid $input-border-color;\n  color: $input-color;\n  font-family: inherit;\n  font-size: $input-label-font-size;\n  padding: $input-form-control-padding-top 0 $input-form-control-padding-bottom;\n  width: 100%;\n\n  // Remove arrow on IE10\n  &::-ms-expand {\n    display: none;\n  }\n\n  &:focus {\n    outline: none;\n  }\n}\n\n.md-selectfield {\n  border: 0;\n  position: relative;\n\n  label {\n    color: $input-label-color;\n    cursor: text;\n    font-size: $label-font-size;\n    left: 0;\n    position: absolute;\n    top: $input-label-top;\n    transform: $input-label-active-transform;\n    transition: $input-label-transition;\n  }\n\n  .md-select {\n    appearance: none;\n  }\n\n  &::after {\n    border-left: .25em solid transparent;\n    border-right: .25em solid transparent;\n    border-top: .375em solid $black;\n    content: '';\n    height: 0;\n    padding: 0;\n    pointer-events: none;\n    position: absolute;\n    right: .5em;\n    top: .75em;\n    width: 0;\n  }\n}\n\n// Bell notifications\n.bell-notification {\n  padding: 0.6em;\n  font-size: 1.0em;\n}\n\n.bell-counter::after {\n  text-shadow: 0 1px 3px rgba(0, 0, 0, 0.3);\n  font-size: 1.6em;\n  font-weight: 700;\n  position: absolute;\n  top: 30px;\n  left: 18px;\n  padding: 5px 8px;\n  line-height: 100%;\n  border-radius: 60px;\n  background: #d00;\n  content: attr(count);\n  -webkit-transform: scale(0.5);\n  transform: scale(0.5);\n  transition: transform, opacity;\n  transition-duration: 0.3s;\n  transition-timing-function: ease-out;\n}\n\n// Refresh button on index page\n.device-row-refresher > .fas:active,\n#refresh-table-content > .fas:active {\n  color: $primary-color;\n}\n\n// Device rows on index table\n.selectable-device-row {\n\n  cursor: pointer;\n}\n\n.not-selectable-device-row:hover {\n\n  background-color: transparent !important;\n}\n\n.hover-effect:hover {\n\n  color: $primary-color-dark;\n}\n\n.dropdown-menu.dropdown-menu-inline.show {\n  display: flex;\n  flex-direction: row;\n}\n\n.md-selectfield.md-selectfield-vlan {\n  padding: 0;\n}\n\n.md-select.md-select-vlan {\n  padding : 0.6rem 1.2rem 0.4rem 0rem;\n}\n\n@media (max-width: 992px) {\n  .container {\n    width: 100%;\n    max-width: none;\n  }\n}\n\n.font-bold {\n  font-weight: bold;\n}\n\n.flash {\n  margin-top: 20px;\n}\n\n.version-label {\n  font-size: 10px;\n}\n\n.md-addon {\n  background-color: transparent;\n  border-color: transparent;\n}\n\n/* Rounded sliders */\n\n.switch {\n  position: relative;\n  display: inline-block;\n  width: 60px;\n  height: 34px;\n}\n\n.switch input {display:none;}\n\n.slider {\n  position: absolute;\n  cursor: pointer;\n  top: 0;\n  left: 0;\n  right: 0;\n  bottom: 0;\n  background-color: #ccc;\n  -webkit-transition: .4s;\n  transition: .4s;\n}\n\n.slider:before {\n  position: absolute;\n  content: \"\";\n  height: 26px;\n  width: 26px;\n  left: 4px;\n  bottom: 4px;\n  background-color: white;\n  -webkit-transition: .4s;\n  transition: .4s;\n}\n\ninput:checked + .slider {\n  background-color: #4db6ac;\n}\n\ninput:focus + .slider {\n  box-shadow: 0 0 1px #4db6ac;\n}\n\ninput:checked + .slider:before {\n  -webkit-transform: translateX(26px);\n  -ms-transform: translateX(26px);\n  transform: translateX(26px);\n}\n\n.slider.round {\n  border-radius: 34px;\n}\n\n.slider.round:before {\n  border-radius: 50%;\n}\n\n/* File input */\n\n.btn-file {\n  position: relative;\n  overflow: hidden;\n}\n.btn-file input[type=file] {\n  position: absolute;\n  top: 0;\n  right: 0;\n  min-width: 100%;\n  min-height: 100%;\n  font-size: 100px;\n  text-align: right;\n  filter: alpha(opacity=0);\n  opacity: 0;\n  outline: none;\n  background: white;\n  cursor: inherit;\n  display: block;\n}\n\n.flashbox-color {\n  background-color: #4db6ac!important;\n}\n\n/* tags search input */\n\n.tags-input {\n  border: none;\n  border-bottom: 1px solid #ced4da;\n  box-shadow: none;\n}\n\n/* nav pills custom colors */\n\n.nav-pills .nav-link:not(.active) {\n  background-color: #4db6ac;\n  color: white;\n}\n\n.nav-pills .nav-link:hover {\n  background-color: #4db6ac;\n  color: #2c6b65; \n}\n\n.nav-pills .nav-link:focus {\n  background-color: #3f968e;\n  color: white;\n}\n\n.nav-pills .nav-link.active {\n  background-color: #3f968e;\n  color: white;\n}\n\n/* pagination used by dataTables plugin */\n.pagination .page-item.active .page-link,\n.pagination .page-item.active .page-link:hover {\n  background-color: #4db6ac;\n  color: white;\n}\n\n/* modal dialog customization */\n.modal-dialog.modal-notify.modal-teal .modal-header {\n  background-color: #4db6ac;\n}\n\n.fa-chevron-down::before,\n.fa-chevron-up::before {\n  cursor: pointer;\n}\n\n/* Stepper color override */\n.bs-stepper .step-trigger:focus {\n  color: #3f968e;\n  outline: none;\n}\n\n.active .bs-stepper-circle {\n  background-color: #4db6ac;\n}\n\n.bs-stepper .step-trigger {\n  padding-top: 0px;\n  padding-bottom: 0px;\n}\n\